import React, { Component } from "react";
import './submitting.scss';
import { connect } from "react-redux";
import submitting from '../../assets/images/Submitting.svg';
import * as Actions from "../../store/User/UserAction";
import { withRouter } from "react-router-dom";
import { bindActionCreators } from "redux";
import LinearProgress from '@material-ui/core/LinearProgress';
export class Submitting extends Component {
  constructor(props) {
    super(props);

    this.state = {
show:false
    
    };
  }
  componentDidMount() {
    window.history.pushState(null, "", window.location.href);
    window.onpopstate = function () {
        window.history.pushState(null, "", window.location.href);
    };
    
    window.addEventListener('beforeunload', this.keepOnPage);

setTimeout(()=>{
this.setState({
  show:true
})
},1000)

  }


  componentDidUnmount() {
    window.onbeforeunload = null;
    this.props.UserAction.addEvent({
      responseId: this.props.responseId,
      event: {
        type: "debug",
        level: 3,
        metadata: { Page: "Submission Page" },
        timestamp: new Date().toISOString(),
      },
    });
  }
  
  componentWillUnmount() {
    window.removeEventListener('beforeunload', this.keepOnPage);
  }
  
  keepOnPage(e) {
    var message = 'Warning!\n\nNavigating away from this page will clear your response.';
    e.returnValue = message;
    return message;
  }



  render() {
   console.log('insideee submitting')
    return (
    <div className="submitting-main">
         <div className="submitting-gif">
        <img src={submitting}  />
        </div>
        <div className="submitting-text">
         
          Submitting <span style={{color:'#04724d'}}> {
        this.props.submittedQuestions && this.props.totalQuestions && this.props.submittedQuestions.length==this.props.totalQuestions ? this.props.totalQuestions : this.props.submittedQuestions.length+1
          }</span> of {this.props.totalQuestions}, Please Wait...

      <div style={{padding:'20px', transform:'rotate(180deg)'}}>
    {
      this.props.progress && this.props.progress>0  && this.state.show?   <LinearProgress
      className="linearProgressInternetLanding"
      variant="determinate"
      value={this.props.progress}
      style={{
          backgroundColor: '#fff',
          borderRadius: '15px',
          height: '5px',
      }}
  /> : null
    }
                                               
      </div>
     {
       this.state.show ?  <div style={{ fontSize:'13px',textAlign:'center' }}>Uploading {this.props.progress && this.props.progress.toFixed(1)}%...</div> : null
     }
             
        </div>

     

                                    
                                           
        
    </div>
    );
  }
}

const mapStateToProps = (state) => {
    return {
      UserReducer: state.UserReducer,
    };
  };
  const mapDispatchToProps = (dispatch) => {
    return {
      UserAction: bindActionCreators(Actions, dispatch),
    };
  };
  export default connect(
    mapStateToProps,
    mapDispatchToProps
  )(withRouter(Submitting));
